<template>
  <div id="main">
    <h2>User Mgmt</h2>
    <b-table :items="users" :fields="userfields" :busy="isBusy" striped hover responsive="sm">
      <template v-slot:cell(isGlobalAdmin)="row">
        <b-checkbox v-model="row.item.isGlobalAdmin" disabled></b-checkbox>
      </template>
      <template v-slot:cell(actions)="row">
        <b-button size="sm" @click="onEditUser(row.item)" class="mr-2">Edit</b-button>
        <b-button
          size="sm"
          @click="onDeleteUser(row.item, $evt)"
          class="mr-2"
          variant="danger"
        >Delete</b-button>
      </template>
    </b-table>
    <b-button
      size="sm"
      @click="showUserModal=true; mode='create'"
      class="mr-2"
      variant="success"
    >Create User</b-button>

    <br />
    <h2>Company Mgmt</h2>
    <b-table :items="tenants" :fields="tenantfields" :busy="isBusy" striped hover responsive="sm">
      <template v-slot:cell(actions)="row">
        <b-button size="sm" @click="onEditTenant(row.item)" class="mr-2">Edit</b-button>
        <b-button
          size="sm"
          @click="onDeleteTenant(row.item)"
          class="mr-2"
          variant="danger"
        >Delete</b-button>
      </template>
    </b-table>
    <b-button size="sm" @click="showTenantModal=true" class="mr-2" variant="success">Create Company</b-button>

    <!-- ONLY MODAL -->
    <b-modal v-model="showUserModal" title="Create/Edit User">
      <b-container fluid>
        <b-form @submit="onCreateOrEditUser">
          <b-form-input v-model="newUserDisplayName" type="text" placeholder="Display Name"></b-form-input>
          <b-form-input v-model="newUserMail" type="text" placeholder="Email"></b-form-input>
          <b-form-input v-model="newUserPassword" type="password" placeholder="Password"></b-form-input>
          <b-form-input v-model="newUserAssignedTenants" type="text" placeholder="AssignedTenants"></b-form-input>
          <b-form-input v-model="newUserisTennantAdmin" type="text" placeholder="isTennantAdmin"></b-form-input>
          <b-form-checkbox v-model="newUserAdmin" type="checkbox">Admin</b-form-checkbox>
          <b-button type="submit" variant="primary" class="text-center">Submit</b-button>
        </b-form>
      </b-container>

      <template v-slot:modal-footer>
        <div></div>
      </template>
    </b-modal>

    <b-modal v-model="showTenantModal" title="Create Company">
      <b-container fluid>
        <b-form @submit="onCreateTenant">
          <b-form-input v-model="newCompanyID" type="text" placeholder="Company ID"></b-form-input>
          <b-form-input v-model="newCompanyName" type="text" placeholder="Company Name"></b-form-input>
          <b-form-input v-model="newCompanyOwner" type="text" placeholder="Owner ID"></b-form-input>
          <b-button type="submit" variant="primary" class="text-center">Submit</b-button>
        </b-form>
      </b-container>

      <template v-slot:modal-footer>
        <div></div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";
import * as utils from "../utils/utils";
//import router from "../router";

export default {
  name: "AdminView",
  data() {
    return {
      showTenantModal: false,
      newCompanyID: "",
      newCompanyName: "",
      newCompanyOwner: "",

      showUserModal: false,
      mode: "create",
      editId: "",
      newUserDisplayName: "",
      newUserMail: "",
      newUserPassword: "",
      newUserAssignedTenants: "",
      newUserisTennantAdmin: "",
      newUserAdmin: Boolean,

      isBusy: false,

      userfields: [
        { key: "id", label: "ID" },
        { key: "displayName", label: "Display Name" },
        { key: "email", label: "Email" },
        { key: "isGlobalAdmin", label: "Admin" },
        { key: "assignedTenants", label: "assignedTenants" },
        { key: "isTennantAdmin", label: "isTennantAdmin" },
        { key: "actions", label: "Actions" }
      ],

      tenantfields: [
        { key: "tenantId", label: "ID" },
        { key: "companyName", label: "Name" },
        { key: "tenantOwner", label: "Owner" },
        { key: "actions", label: "Actions" }
      ],

      users: [],
      tenants: []
    };
  },
  computed: {},
  methods: {
    //USER
    onCreateOrEditUser(evt) {
      evt.preventDefault();

      const data = {
        user: this.newUserDisplayName,
        email: this.newUserMail,
        pw: this.newUserPassword,
        isGlobalAdmin: this.newUserAdmin,
        assignedTenants: JSON.parse(this.newUserAssignedTenants),
        isTennantAdmin: JSON.parse(this.newUserisTennantAdmin),
      };

      if (this.mode === "create") {
        axios
          .post("/api/users/", data)
          .then(response => {
            this.$log.debug("create okay " + response.status);
            this.clearUserModel();
          })
          .catch(errors => {
            this.$log.error("create fail " + errors);
            this.$bvToast.toast(errors.message, {
              title: `API Error`,
              variant: "danger",
              solid: true
            });
          });
      } else {
        if (data.pw.startsWith("$2b")) {
          delete data.pw;
        }

        axios
          .post("/api/users/" + this.editId, data)
          .then(response => {
            this.$log.debug("udapte okay " + response.status);
            this.clearUserModel();
          })
          .catch(errors => {
            this.$log.error("udapte fail " + errors);
            this.$bvToast.toast(errors.message, {
              title: `API Error`,
              variant: "danger",
              solid: true
            });
          });
      }
    },

    clearUserModel() {
      this.showUserModal = false;
      this.newUserDisplayName = "";
      this.newUserMail = "";
      this.newUserPassword = "";
      this.newUserAssignedTenants = "[ \"10\" ]";
      this.newUserisTennantAdmin = "[ \"10\" ]";
      this.editId = "";

      this.refreshTables();
    },

    onEditUser(item) {
      this.mode = "edit";
      this.newUserDisplayName = item.displayName;
      this.newUserMail = item.email;
      this.newUserPassword = item.password;
      this.newUserAdmin = item.isGlobalAdmin;
      this.newUserAssignedTenants = item.assignedTenants;
      this.newUserisTennantAdmin = item.isTennantAdmin;
      this.editId = item.id;

      this.showUserModal = true;
    },

    onDeleteUser(item) {
      axios
        .delete("/api/users/" + item.id)
        .then(response => {
          this.$log.debug("delete okay " + response.status);
          this.refreshTables();
        })
        .catch(errors => {
          this.$log.error("delete fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });
    },

    //TENNANT STUFF
    onCreateTenant(evt) {
      evt.preventDefault();

      const data = {
        tenId: this.newCompanyID,
        companyName: this.newCompanyName,
        tenantOwner: this.newCompanyOwner
      };
      axios
        .post("/api/tenants/", data)
        .then(response => {
          this.$log.debug("create okay " + response.status);
          this.showTenantModal = false;
          this.newCompanyID = "";
          this.newCompanyName = "";
          this.newCompanyOwner = "";

          this.refreshTables();
        })
        .catch(errors => {
          this.$log.error("create fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });
    },

    onEditTenant(item) {
      this.newCompanyName = item.companyName;
    },

    onDeleteTenant(item) {    
      console.log(item)
      
      axios
        .delete("/api/tenants/" + item.tenantId)
        .then(response => {
          this.$log.debug("delete okay " + response.status);
          this.refreshTables();
        })
        .catch(errors => {
          this.$log.error("delete fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });
    },

    refreshTables() {
      this.isBusy = true;

      axios
        .get("/api/users/")
        .then(response => {
          this.$log.debug("Users");
          this.$log.debug(response.data);
          this.users = response.data.users;
        })
        .catch(errors => {
          this.$log.error("get users fail " + errors);
          this.$bvToast.toast(errors.message, {
            title: `API Error`,
            variant: "danger",
            solid: true
          });
        });

      axios
        .get("/api/tenants/")
        .then(response => {
          this.$log.debug("Tenants");
          this.$log.debug(response.data);
          this.tenants = response.data.tenants;
        })
        .catch(errors => this.handleAPIError(errors));

      this.isBusy = false;
    },

    handleAPIError(errors) {
      this.$log.error("api call fail " + errors);
      this.$bvToast.toast(errors.message, {
        title: `API Error`,
        variant: "danger",
        solid: true
      });
    }
  },
  mounted() {
    utils.checkForLoginAndRedirect(this.$router, this.$store);

    this.refreshTables();
    this.clearUserModel();
    this.newCompanyOwner = this.$store.state.userObj.id;
  }
};
</script>

<style scoped>
#main {
  width: 85%;
  margin: auto;
}
</style>
